<template>
  <el-row :gutter="12">
    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="btc_signMessage()">signMessage(BTC)</el-button>
    </el-col>
    <div class="line"></div>
    <el-col :span="24">
      <div style="font-size: 12px; color: gray;">(发送到Pattern测试钱包的legacy地址)</div>
      <el-button class="grid-content" type="primary" @click="btc_send()">send(BTC)</el-button>
    </el-col>
    <div class="line"></div>
    <el-col :span="24">
      <div style="font-size: 12px; color: gray;">(请使用Rally钱包taproot地址)</div>
      <el-button class="grid-content" type="primary" @click="btc_signPsbt_up()">signPsbt(上架NFT)</el-button>
      <br>
      <el-button class="grid-content" type="primary" @click="btc_signPsbt_down()">signPsbt(下架NFT)</el-button>
    </el-col>
    <div class="line"></div>
    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="btc_signPsbts()">signPsbts(BTC)</el-button>
    </el-col>
    <div class="line"></div>
    <el-col :span="24">
      <div style="font-size: 12px; color: gray;">(NFT发送到Pattern测试钱包的legacy地址)</div>
      <el-button class="grid-content" type="primary" @click="btc_sendInscription()">sendInscription(BTC)</el-button>
      <el-input class="grid-content" v-model="nftId" placeholder="请输入要转移的NFT ID" />
      <div style="font-size: 12px;margin-top: 12px; color: gray;">
        <li>
          1. 在web端NFT界面，选择要发送的NFT，打开详情
        </li>
        <li>
          2. 从NFT详情页URL中获取NFT id（url最后一部分）
        </li>
        <li>
          3. 复制到上面输入框中，再点击sendInscription按钮
        </li>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import { ref } from 'vue';

export default {
  name: 'BtcTransactionPanel',
  props: {
    msg: String
  },
  methods: {
    btc_signMessage() {
      let params = {
        from: "bc1p7pgnqe87red4cvd7ml6rh9pl9ufpr522k2y3dpeyrvfc6g2g3r3s3ae9dr",
        signStr: "Welcome to UniSat!\n\nClick to sign in and accept the UniSat Terms of Service (https://unisat.io/terms-of-service.html) and Privacy Policy (https://unisat.io/privacy-policy.html).\n    \nThis request will not trigger a blockchain transaction.\n    \nYour authentication status will reset after 24 hours.\n    \nWallet address:\nbc1p7pgnqe87red4cvd7ml6rh9pl9ufpr522k2y3dpeyrvfc6g2g3r3s3ae9dr\n    \nNonce:\n162d70e3-8c21-4cfd-9a22-9bf0a4b7be51\n"
      };
      this.$emit('generateTransactionParams', params, "btc_signMessage")
    },

    btc_send() {
      let params = {
        from: 'bc1p4k9ghlrynzuum080a4zk6e2my8kjzfhptr5747afzrn7xmmdtj6sgrhd0m',
        to: '1NKnZ3uAuQLnmExA3WY44u1efwCgTiAxBn', // pattern测试钱包的legacy地址
        value: '0.0000016'
      }
      this.$emit('generateTransactionParams', params, "btc_send")
    },

    btc_signPsbt_up() {
      // 上架okrc.btc
      let psbt = ""
      this.$emit('generateTransactionParams', {
        psbtHex: psbt,
        options: { autoFinalized: 0 }
      }, "btc_signPsbt")
    },

    btc_signPsbt_down() {
      // 下架okrc.btc
      let psbt = ""

      this.$emit('generateTransactionParams', {
        psbtHex: psbt,
        options: { autoFinalized: 0 }
      }, "btc_signPsbt")
    },

    btc_signPsbts_up() {
      // 上架 
      /// pizza test
      let psbt1 = ""
      /// fgbbb
      let psbt2 = "'"

      this.$emit('generateTransactionParams', {
        psbtHexs: [psbt1, psbt2],
        options: { autoFinalized: 0 }
      }, "btc_signPsbts")
    },

    btc_signPsbts_down() {
      // 下架
      /// pizza test
      let psbt1 = ""
      let psbt2 = ""

      this.$emit('generateTransactionParams', {
        psbtHexs: [psbt1, psbt2],
        options: { autoFinalized: 0 }
      }, "btc_signPsbts")
    },

    btc_sendInscription() {
      if (!this.nftId.value) {
        alert("请输入NFT ID")
        return
      }
      this.$emit('generateTransactionParams', {
        address: '1NKnZ3uAuQLnmExA3WY44u1efwCgTiAxBn', // pattern测试钱包的legacy地址
        inscriptionId: '',
        options: {
          feeRate: 15
        }
      }, "btc_sendInscription")
    }
  },
  setup() {
    console.log('setup执行了')
    const nftId = ref('')

    return {
      nftId
    }
  },
}
</script>

